import React, { useState } from 'react';
import { Modal, Box, Typography, FormControl, FormLabel, RadioGroup, FormControlLabel, Radio, TextField, Checkbox, Button, Stack } from '@mui/material';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DesktopDatePicker } from '@mui/x-date-pickers/DesktopDatePicker';
import dayjs from 'dayjs';
import { createBooking } from '../services/api';

const HotelBookingModal = ({ hotel, open, onClose }) => {
  const [roomType, setRoomType] = useState('100');
  const [breakfastIncluded, setBreakfast] = useState(false);
  const [guests, setGuests] = useState(1);
  const [checkInDate, setCheckInDate] = useState(dayjs());
  const [checkOutDate, setCheckOutDate] = useState(dayjs().add(1, 'day'));


  const handleBooking = async () => {
    const booking = JSON.stringify({
        hotelId: hotel.id,
        roomType: Number(roomType),
        breakfastIncluded,
        checkInDate: checkInDate.format('YYYY-MM-DD'),
        checkOutDate: checkOutDate.format('YYYY-MM-DD'),
        guests,
    });

    try {
      const response = await createBooking(booking);
      console.log("Booking successful:", response);
      onClose();
    } catch (error) {
      console.error("Error creating booking:", error);
    }
    onClose();
  };

  return (
    <Modal open={open} onClose={onClose}>
      <Box sx={{ position: 'absolute', top: '50%', left: '50%', transform: 'translate(-50%, -50%)', width: 400, bgcolor: 'background.paper', p: 4, borderRadius: 2 }}>
        <Typography variant="h5" id="modal-title" gutterBottom>
          {hotel.name}
        </Typography>
        <Typography variant="body2" color="text.secondary" gutterBottom>
          {hotel.location}
        </Typography>
        <img src={hotel.imageUrl} alt={hotel.name} style={{ width: '100%', marginBottom: '1rem' }} />

        <Box sx={{ display: 'flex', flexDirection: 'column' }}>
          <FormControl component="fieldset">
            <FormLabel component="legend">Room Type</FormLabel>
            <RadioGroup value={roomType} onChange={(e) => setRoomType(e.target.value)}>
              <FormControlLabel value="100" control={<Radio />} label="Standard" />
              <FormControlLabel value="150" control={<Radio />} label="Deluxe" />
              <FormControlLabel value="200" control={<Radio />} label="Suite" />
            </RadioGroup>
          </FormControl>

          <FormControlLabel
            control={<Checkbox checked={breakfastIncluded} onChange={() => setBreakfast(!breakfastIncluded)} />}
            label="Include Breakfast"
          />
        </Box>
        

        <Stack direction="row" spacing={2} mt={2}>
          <LocalizationProvider dateAdapter={AdapterDayjs}>
            <DesktopDatePicker
              label="Check-In Date"
              value={checkInDate}
              onChange={(newValue) => setCheckInDate(newValue)}
              renderInput={(params) => <TextField {...params} />}
              disablePast
            />
          </LocalizationProvider>
          <LocalizationProvider dateAdapter={AdapterDayjs}>
            <DesktopDatePicker
              label="Check-Out Date"
              value={checkOutDate}
              onChange={(newValue) => setCheckOutDate(newValue)}
              renderInput={(params) => <TextField {...params} />}
              minDate={checkInDate.add(1, 'day')}
            />
          </LocalizationProvider>
        </Stack>

        <TextField
          label="Number of Guests"
          type="number"
          value={guests}
          onChange={(e) => setGuests(Number(e.target.value))}
          fullWidth
          margin="normal"
          mt={2}
        />

        <Button variant="contained" color="primary" onClick={handleBooking} fullWidth mt={2}>
          Book Now
        </Button>
      </Box>
        
      
    </Modal>
  );
};

export default HotelBookingModal;
