import axios from 'axios';

const API_BASE_URL = 'https://localhost:7285/api';

export const getHotels = async (location = '') => {
  try {
    const response = await axios.get(`${API_BASE_URL}/Hotel`, { params: { location } });
    return response.data;
  } catch (error) {
    console.error('Error fetching hotels:', error);
    throw error;
  }
};

export const getHotelById = async (id) => {
  try {
    const response = await axios.get(`${API_BASE_URL}/Hotel/${id}`);
    return response.data;
  } catch (error) {
    console.error('Error fetching hotel:', error);
    throw error;
  }
};

export const getBookings = async () => {
  try {
    const response = await axios.get(`${API_BASE_URL}/Booking`);
    return response.data;
  } catch (error) {
    console.error('Error fetching bookings:', error);
    throw error;
  }
};

export const createBooking = async (booking) => {
  try {
    const response = await axios.post(`${API_BASE_URL}/Booking`, booking, {
      headers: {
        'Content-Type': 'application/json'
      }
    });
    return response.data;
  } catch (error) {
    console.error('Error creating booking:', error);
    throw error;
  }
};