import React, { useState } from 'react';
import { Card, CardContent, CardMedia, Typography, Grid, Button, TextField, InputAdornment } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import HotelBookingModal from '../components/HotelBookingModal';
import Box from '@mui/material/Box';
import { getHotels } from '../services/api';

const HotelList = ({ hotels, setHotels }) => {
  const [selectedHotel, setSelectedHotel] = useState(null);
  const [modalOpen, setModalOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');

  const handleOpenModal = (hotel) => {
    setSelectedHotel(hotel);
    setModalOpen(true);
  };

  const handleCloseModal = () => {
    setModalOpen(false);
  };

  const handleSearch = async () => {
    const data = await getHotels(searchQuery);
    setHotels(data);
  };

  const handleKeyPress = async (event) => {
    if (event.key === 'Enter') {
      handleSearch();
    }
  };

  return (
    <>
      <Box sx={{ display: 'flex', justifyContent: 'center' }}>
        <TextField 
          label="Search by Location" 
          variant="outlined"
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          onKeyDown={handleKeyPress}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SearchIcon /> 
              </InputAdornment>
            ),
          }}
          sx={{ marginBottom: 2, display: 'flex', width: '20%' }} // Add margin below the search bar
        />
        <Button variant="contained" color="primary" onClick={handleSearch} sx={{ marginBottom: 2, marginLeft: 1}}>
          Search
        </Button>
      </Box>
      <Grid container spacing={3} sx={{ padding: 2 }}>
        {hotels.map((hotel) => (
          <Grid item xs={12} sm={6} md={4} key={hotel.id}>
            <Card
              elevation={4}
              sx={{
                borderRadius: 3,
                overflow: 'hidden',
                transition: 'transform 0.3s',
                '&:hover': { transform: 'scale(1.02)' },
              }}
            >
              <CardMedia
                component="img"
                height="200"
                image={hotel.imageUrl}
                alt={hotel.name}
              />
              <CardContent sx={{ padding: 2 }}>
                <Typography variant="h6" component="div" fontWeight="bold">
                  {hotel.name}
                </Typography>
                <Typography variant="body2" color="text.secondary" gutterBottom>
                  {hotel.location}
                </Typography>
                <Grid container justifyContent="center" alignItems="center">
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => handleOpenModal(hotel)}
                    size="large"
                    fullWidth
                    sx={{ 
                      textTransform: 'none',
                      borderRadius: 2,
                      backgroundColor: '#007bff',
                      '&:hover': { backgroundColor: '#0056b3' },
                    }}
                  >
                    Book Now
                  </Button>
                </Grid>
              </CardContent>
            </Card>
          </Grid>
        ))}
      </Grid>

      {selectedHotel && (
        <HotelBookingModal
          hotel={selectedHotel}
          open={modalOpen}
          onClose={handleCloseModal}
        />
      )}
    </>
  );
};

export default HotelList;
