import React from 'react';
import { List, ListItem, ListItemText, Typography, Paper, Box } from '@mui/material';
import { CheckCircle as CheckCircleIcon } from '@mui/icons-material'; // Added icon

const BookingList = ({ bookings = [] }) => {
  return (
    <Paper elevation={3} style={{ padding: 16, borderRadius: 8 }}>
      <Typography variant="h6" gutterBottom>
        Your Bookings
      </Typography>

      <List disablePadding>
        {bookings.map((booking) => (
          <Box key={booking.id} sx={{ mb: 2, border: '1px solid #eee', borderRadius: 2, p: 2 }}> {/* Added box for each booking */}
            <ListItem sx={{ display: 'flex', alignItems: 'center' }}>
              <CheckCircleIcon color="success" sx={{ mr: 1 }} /> {/* Added icon */}
              <ListItemText 
                primary={
                  <Typography variant="body1">
                    <b>{booking.hotelName}</b>
                  </Typography>
                }
                secondary={
                  <React.Fragment>
                    <Typography component="span" variant="body2">
                      Room: {booking.roomType} | Breakfast: {booking.breakfastIncluded ? 'Yes' : 'No' } | Guests: {booking.guests}
                    </Typography>
                    <Typography component="span" variant="body3" sx={{ mt: 0.5, display: 'block' }} > 
                      Stay Dates: {booking.checkInDate} - {booking.checkOutDate} 
                    </Typography>
                    <Typography component="span" variant="subtitle1" sx={{ fontWeight: 600, mt: 0.5, display: 'block' }}> 
                      Total: €{booking.totalPrice}
                    </Typography>
                  </React.Fragment>
                }
              />
            </ListItem>
          </Box>
        ))}

        {!bookings.length && (
          <ListItem>
            <ListItemText primary="No bookings found." />
          </ListItem>
        )}
      </List>
    </Paper>
  );
};

export default BookingList;
